@import "styles/styles";

.account {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    padding-top: 12px;
    text-align: center;

    &__message {
        @include for-alert;

        &__button {
            border: 0;
            padding: 5px 10px;
            margin-bottom: 5px;
        }

        &__text {
            padding: 12px 24px;
        }
    }

    &__text {
        width: 300px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 12px;
        width: 300px;
    }

    &__input {
        &:focus {
            outline-color: red;
        }
    }

    &__button {
        width: 100%;
        justify-content: center;
    }

}

.show {
    opacity: 1;
    transform: translateY(0);
    transition: all 0.5s ease;
}